<template>
    <div class="random-links">
      <div v-if="$route.name === '推广链接'">
        <Button type="success" size="large" to="/manage/links/spread/create" class="create">添加新链接</Button>
        <Table class="spread-table" :columns="spreadHeader" :data="spreadData"></Table>
      </div>
      <router-view></router-view>
    </div>
</template>

<script>
export default {
  data() {
    return {
      spreadData: [],
      isShow: false,
      isUpdate: false,
      domain_names: [],
      spreadHeader: [
        {
          title: "链接",
          align: "center",
          render: (h, params) => {
            let url = 'http://erji.nyzda.top/transfer/' + params.row.id
            return h( "span", {
                props: {}
              }, url);
          }
        },
        {
          key: "title",
          title: "标题",
          align: "center"
        },
        {
          title: "Action",
          align: "center",
          render: (h, params) => {
            let that = this;
            return h("div", [
              h( "Button", {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$router.push({
                        name: "修改推广链接",
                        params: {
                          row: params.row,
                          index: params.index
                        }
                      });
                    }
                  }
                }, "修改" ),
              h( "Button", {
                  props: {
                    type: "error",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.deleteOne(params.row, params.index);
                    }
                  }
                }, "删除" )
            ]);
          }
        }
      ]
    };
  },
  mounted() {
    this.showList();
  },
  methods: {
    deleteOne(row, index) {
      let that = this;
      this.$Modal.confirm({
        title: '提示:',
        content: '确认删除这条内容吗？',
        onOk: () => {
          that.$axios.get('/transfer/delete', {params: {id: row._id}})
          .then(res => {
            that.$Message.info(res.data.success);
            that.spreadData.splice(index, 1)
          });
        }
      })
    },
    showList() {
      this.$axios.get('/transfer')
      .then(res => {
        this.spreadData = res.data.messages;
        this.domain_names = res.data.domain_names;
      });
    }
  },
  watch: {
    $route(to, from) {
      if(this.$route.params.state != 'cancel') {
        if(from.name == '创建推广链接') {
          this.spreadData.push(this.$route.params)
        } else if (from.name == '修改推广链接') {
          this.spreadData.splice(this.$route.params.index, 1, this.$route.params.row)
        }
      }
    }
  }
};
</script>


<style scoped>
.spread-table {
  margin: 30px 0;
}
</style>
